import React, { Component } from "react";
import { MyMenu } from "./components/menu";
import { Switch, Route } from "react-router-dom";
import posed, { PoseGroup } from "react-pose";
import { Home } from "./components/home";
import { jsx, css } from "@emotion/core";
import { mainFontFamily } from "./constants/fonts";
import { Projects } from "./components/projects";
import { mainBGColor } from "./constants/colors";
import { Project } from "./components/project";
import { About } from "./components/about";
import { Blog } from "./components/blog";
import { HelloScreen } from "./components/helloScreen";
/** @jsx jsx */

const RouteContainer = posed.div({
  enter: { opacity: 1, delay: 300 },
  exit: { opacity: 0, delay: 300 }
});

const modalStyling = css({
  background: mainBGColor,
  position: "absolute",
  top: 0,
  bottom: 0,
  right: 0,
  left: 0,
  zIndex: 5
});

const appContainerStyling = css({
  minHeight: "100vh",
  fontFamily: mainFontFamily,
  display: "flex",
  flexWrap: "wrap",
  flexFlow: "column",
  backgroundColor: mainBGColor
});

const routeContainerStyling = css({
  display: "flex",
  width: "100%",
  flexGrow: 1
});

export class App extends Component {
  state = {
    isHellloScreenVisible: true
  };

  render() {
    const { location } = this.props;
    return (
      <div css={appContainerStyling}>
        <MyMenu />
        <PoseGroup>
          <RouteContainer css={routeContainerStyling} key={location.pathname}>
            <Switch key="switch" location={location}>
              <Route
                exact
                path="/projects"
                key="projects"
                component={Projects}
              />
              <Route
                path="/projects/:name"
                key="single_project"
                component={Project}
              />
              <Route path="/about" key="about" component={About} />
              <Route path="/blog" key="blog" component={Blog} />
              <Route key="home" path="/" component={Home} />
            </Switch>
          </RouteContainer>
        </PoseGroup>
      </div>
    );
  }
}

export default App;
