/** @jsx jsx */

import React, { Component } from "react";
import posed, { PoseGroup } from "react-pose";
import { jsx, css } from "@emotion/core";
import { mainYellow, mainBGColor } from "../constants/colors";
import SplitText from "react-pose-text";
import { mainFontFamily } from "../constants/fonts";

const Modal = posed.div({
  enter: {
    opacity: 1,
    delay: 300,
    transition: {
      default: { duration: 300 }
    },
    beforeChildren: true,
    staggerChildren: 50
  },
  exit: {
    opacity: 0,
    transition: { duration: 500 }
  }
});

const nameStyling = css({
  fontSize: "2.5rem"
});

const nameCharPoses = {
  exit: { opacity: 0 },
  enter: { opacity: 1 }
};

const helloScreen = css({
  width: "100%",
  height: "100vh",
  backgroundColor: mainBGColor,
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  fontSize: "3rem",
  fontFamily: mainFontFamily
});

export class HelloScreen extends Component {
  state = {
    isVisible: false,
    names: ["Karol Gruszka", "Sniadek"],
    currentIndex: 0
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        isVisible: !this.state.isVisible
      });
    }, 1);

    setTimeout(() => {
      this.setState({
        isVisible: !this.state.isVisible
      });
    }, 2500);
  }

  render() {
    const { isVisible } = this.state;
    return (
      <PoseGroup>
        {isVisible && (
          // If animating more than one child, each needs a `key`
          <Modal key="modal" css={helloScreen}>
            <SplitText
              initialPose="exit"
              pose="enter"
              key="Karol Gruszka"
              charPoses={nameCharPoses}
            >
              Welcome!
            </SplitText>
          </Modal>
        )}
      </PoseGroup>
    );
  }
}
